.wrapper {
  width: 100%;
  z-index: -1;
  position: absolute;
}

.visible {
  transition-timing-function: ease-in;
  transition: .5s;
  opacity: 1;
  }

.notvisible {
  opacity: 0;
  transition-timing-function: ease-in;
  transition: .5s;
}

NavBar {
  right: 0;
  z-index: 1;
  position: fixed;
   display: grid;
   background-color: #0f0f0f;
   width: 100%;
}

* {
  font-family: 'Poppins', sans-serif;
  /* color: #F2EDD7FF; */
  color: white;

}

li {
  display: inline;
  position: relative;
}

a {
  overflow: hidden;
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: 600px;
  /* animation: color-change 6s infinite; */
  letter-spacing: 5px;
  margin-right: 20px;
  border-right: 90px;
  float: right;
  margin-top: 20px;
  padding: 10px;
}

a:hover {

  
  -webkit-text-stroke: 1.5px rgb(255, 255, 255);
}

/* Hide scrollbar for Chrome, Safari and Opera */
body::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
body {
  /* IE and Edge */
  -ms-overflow-style: none;
  /* Firefox */
  scrollbar-width: none;
}

/* title */
h1 {
  top: 0;
  font-size: 200px;
  font-weight: 750;
  margin-top: 0px;
  padding-top: 125px;
  padding-left: 50px;
  padding-bottom: 25%;
  margin: 0px;
  opacity: 1;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke: 5px;
  position: relative;
}

h2 {
  opacity: 1;
  font-size: 40px;
  padding-top: 50px;
  padding-left: 100px;
  margin-bottom: 20px;
  margin-top: 1000px;
}

p {
  font-size: 20px;
  font-weight: 400;
  float: left;
  opacity: 0;
  padding: 0px 300px 15px 50px;
  margin-top: 0px;
  display: inline-block;
  overflow-wrap: break-word;
}

